@mixin mq1 {
    @media (min-width: #{$telefono}) {
        @content;
    }
}
@mixin mq2 {
    @media (min-width: #{$tablet}) {
        @content;
    }
}
@mixin mq3 {
    @media (min-width: #{$desktop}) {
        @content;
    }
}
@mixin mq4 {
    @media (min-width: 290px) {
        @content;
    }
}
@mixin mq5 {
    @media (max-width: 600px) {
        @content;
    }
}
@mixin grid($columnas, $espaciado) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}
@mixin grid2($espaciado) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: 1fr 2fr;
}
@mixin grid3($espaciado) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: 2fr 1fr;
}
@mixin bgOpaco($url, $degradado) {
    background-image: linear-gradient($degradado 0%, $degradado 100%), url($url);
    background-repeat: no-repeat;
    background-size: cover;
}

@mixin scroll {
    overflow-y: hidden;
    overflow-x: scroll;
    scroll-snap-type: x mandatory;

    > * {
        scroll-snap-align: center;
    }
}

@mixin boton($color, $texto, $medida) {
    background-color: $color;
    font-size: 2rem;
    padding: $medida;
    border-radius: 1rem;
    font-weight: 700;
    display: block;
    width: 100%;
    text-align: center;
    color: $texto;
    text-decoration: none;
    transition: all .2s ease;
    border: none;
    &:hover {
        cursor: pointer;
        background-color: lighten($color, 5);
    }
    @include tablet {
        width: auto;
    }
}

@mixin headLineaAtras($color) {
    display: inline-block;
    &::after {
        content: '';
        background-color: $color;
        height: 1rem;
        width: calc(100% + 1rem);
        display: block;
        margin: -1.4rem 0 0 0;
    } 
}

@mixin headLineaAbajo {
    &::after {
        content: '';
        display: block;
        height: 1rem;
        width: 15rem;
        margin-top: 1rem;
        background-image: linear-gradient(to right, v.$azulf 0%, v.$azulf 50%,
        lighten(v.$azulf, 20%) 50%, lighten(v.$azulf, 20%) 100%);
    }
}

@mixin headCursiva($color) {
    font-family: v.$fcursiva;
    font-weight: 900;
    color: v.$negro;
    font-size: 2rem;
    line-height: 1;
    span {
        display: block;
        font-family: v.$fsecundaria;
        color: $color;
    }
}