.not-webp .inicio {
    @include bgOpaco ('/build/img/header.jpg', $opaco);
}
.webp .inicio {
    @include bgOpaco ('/build/img/header.webp', $opaco);
}

.inicio {
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    h1 {
        color: $blanco;
        text-align: center;
        line-height: normal;
        font-weight: 900;
        font-size: 2.5rem;
        @include mq2 {
            text-align: left;
            font-size: 3.8rem;
        }
    }
}
.ext {
    background-color: $negro;
}
.barra {
    
    .navegacion {
        padding: 2rem;
        display: flex;
        flex-direction: column;
        align-items: center;

        @include mq2 {
            flex-direction: row;
            justify-content: space-between;
            align-items: flex-end;
        }
    }
    .enlaces {
        display: flex;
        flex-direction: column;
        text-align: center;
        visibility: hidden;
        opacity: 0;
        height: 0;
        transition: visibility .3s ease-in-out, opacity 0.5s linear, height 3s linear;
        
        @include mq2 {
            flex-direction: row;
            align-items: center;
            visibility: visible;
            opacity: 1;
        }
    }
    .bot {
        font-size: 1.8rem;
        font-family: $fuente_secundaria;
        font-weight: 900;
        color: $blanco;
        @include mq2 {
        margin-right: 1rem;
        }
        &:last-of-type {
            margin-right: 0;
        }
    }
    .bot:hover {
        cursor: pointer;
        color: $amarilo;
    }
    .mostrar {
        visibility: visible;
        opacity: 1;
        height: auto;
        display: flex;
    }
}
.derecha {
    display: flex;
    flex-direction: column;
    align-items: center;
    .derecha-bot {
        margin-bottom: 1.5rem;
    }
    @include mq2 {
        align-items: flex-end;
    }
//! Crear selector modo nocturno
    .dark-mode-bot {
        margin-bottom: 1rem;
        border-radius: 100rem;
        border: none;
        position: relative;
        display: flex;
        outline: none;
        background: $gris;
        &::after {
            content: "";
            display: block;
            width: 2.8rem;
            height: 2.7rem;
            position: absolute;
            background-color: $blanco;
            top: 0;
            left: 0;
            right: unset;
            border-radius: 10rem;
            transition: 4s linear;
            margin-bottom: 1rem;
        }
        &.active {
            background: $gris;
            &::after {
                right: 0;
                left: unset;
            }
        }
    }
    &:hover {
        cursor: pointer;
    }
    span {
        display: block;
    }
}

/** -Boton Hamburguesa- **/

.mobile-menu {
    @include mq2 {
        display: none;
    }
    img {
        width: 4rem;
    }
}
