.boton {
    padding: 1.5rem;
    background-color: $verde;
    border: none;
    margin-bottom: 1rem;
    margin-top: 1rem;
    color: $blanco;
    &:hover {
        cursor: pointer;
    }
}
.boton-rojo {
    padding: 1.5rem;
    background-color: $rojo;
    border: none;
    margin-bottom: 1rem;
    margin-top: 1rem;
    color: $blanco;
    width: 100%;
    font-weight: bold;
    &:hover {
        cursor: pointer;
    }
}
.boton-naranja {
    padding: 1.5rem;
    background-color: orangered;
    border: none;
    margin-bottom: 1rem;
    margin-top: 1rem;
    color: $blanco;
    &:hover {
        cursor: pointer;
    }
}