.dark-mode {
    background-color: darken($negro, $amount: 20);
    p {
        color: $blanco;
    }
    h1,h2,h3,h4 {
        color: $gris;
    }
    .anuncio-contenido {
        background-color: $negro;
        border: 1px solid $gris ;
        li {
            img {
                filter: invert(100%);
            }
        }
        
        .precio {
            color: greenyellow;
        }
    }
    .texto-blog {
        h1 {
            a {
                color: $gris;
            }
        }
    }
    .formulario {
        legend, label {
            color: $gris;
        }
        input:not([type="submit"]),
        textarea,
        select {
            display: block;
            color: $blanco;
            background-color: $gris-oscuro;
            border: 2px solid $negro !important;
        }
    }
    .testimonio {
        p {
            color: $blanco;
        }
        background-color: green !important;
        .relato {
            color: $blanco;
        }
    }
    .propiedades {
        tbody {
            td {
                color: $gris;
            }
        }
    }
}
