.header {
    padding: 1rem 0 3rem 0;
    &.inicio {
        background-position: center center;
        background-size: cover;
        @include mq2 {
            min-height: 70rem;
        }
        .cont-header {
            @include mq2 {
                min-height: 70rem;
            }
        }
    }
}
.cont-header {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    .info1-texto {
        font-family: $fuente_secundaria;
        color: $blanco;
        font-size: 3.2rem;
    }
    
}
.navegacion-enlaces {
    display: flex;
    flex-direction: column;
    text-align: center;

    @include mq2 {
        flex-direction: row;
    }
}