.seccion {
    h1 {
        font-family: $fuente_principal;
        font-weight: bold;
        line-height: normal;
    }
    .anuncios {
        margin-bottom: .5rem;
        @include mq2{
            @include grid(3, $espaciado);
        }
    }
    .bot-extra {
        display: flex;
        justify-content: flex-end;
        margin-bottom: 1rem;
    }
    .bot3 {
        background-color: green;
        color: $blanco;
        padding: 1.5rem;
        &:hover {
            cursor: pointer;
            background-color: olivedrab;
        }
    }
}
.anuncio-contenido {
    background-color: $gris;
    margin-bottom: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    h3 {
        font-family: $fuente_secundaria;
        margin-bottom: 0;
        margin-top: 1rem;
        line-height: normal;
    }
    .descripcion {
        font-family: $fuente_parrafos;
        text-align: justify;
        margin: 1rem;
    }
    .precio {
        color: $verde;
        margin: 1rem;
        font-weight: bold;
        font-size: 2.2rem;
    }
    .bot2 {
        text-align: center;
        display: block;
        width: 100%;
        padding: 1.5rem;
        background-color: orange;
        color: $blanco;
        text-transform: uppercase;
        margin-bottom: .5rem;
        &:hover {
            cursor: pointer;
            background-color: orangered;
        }
    }
    .img-pri {
        height: 42rem;
    }
    .ico-car {
        display: flex;
        list-style: none;
        padding: 0;
        li {
            flex: 1;
            display: flex;
            img {
                flex: 0 0 5rem;
                margin: .5rem;
            }
            p {
                font-weight: bold;
            }
        }
    }
}