.contacto {
    .contacto-titulo {
        h1 {
            font-weight: bold;
            font-family: $fuente_principal;
        }
    }
    .contacto-formulario {
        h2 {
            font-weight: bold;
            margin: 1rem;
            font-family: $fuente_principal;
        }
    }
}
.formulario {
    margin-top: 1rem;
    p {
        margin: 1rem 0;
        font-family: $fuente_parrafos;
        font-weight: 500;
        font-size: 1.8rem;
    }
    legend {
        font-family: $fuente_secundaria;
        font-size: 1.8rem;
    }
    label {
        font-weight: bold;
        font-family: $fuente_principal;
        display: block;
        text-transform: uppercase;
    }
    input:not([type="submit"]),
    textarea,
    select {
        padding: 1rem;
        display: block;
        width: 100%;
        border: 2px solid $gris;
    }
    textarea {
        height: 15rem;
    }
    .flex-bot {
        display: flex;
        justify-content: flex-end;
    }
    .button-form {
        background-color: $amarilo;
        border: none;
        padding: 1.5rem;
        margin: 1rem 0;
        &:hover {
            cursor: pointer;
            background-color: green;
        }
    }
    .forma-contacto {
        max-width: 45rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    input [type="radio"] {
        margin: 0;
        width: auto;
    }
}