.alerta {
    margin: 1rem 0;
    padding: .5rem;
    color: $blanco;
    text-transform: uppercase;
    text-align: center;
    &.error {
        background-color: $rojo;
        margin-top: 1.5rem;
        margin-bottom: 1.5rem;
    }
    &.exito {
        background-color: $amarilo;
        margin-bottom: 1.8rem;
    }
}

table.propiedades {
    margin-top: 2rem;
    width: 100%;
    border-spacing: 0;

    thead {
        background-color: $verde;

        th {
            color: $blanco;
            padding: 2rem;
            text-transform: uppercase;
        }
    }
    tbody {

        td {
            text-align: center;
        }
        a {
            display: block;
        }
    }

    .img-tabla {
        width: 12rem;
        margin-left: 50%;  
        transform: translateX(-50%);
    }
}
.img-act {
    width: 13rem;
    margin: 1rem 0;
}
@include mq5 {
    table.propiedades {
        width:100%;
    }
    thead {
        display: none;
    }
    tr:nth-of-type(2n) {
        background-color: inherit;
    }
    tr td:first-child {
        font-weight:bold;
        font-size:1.3em;
    }
    tbody td {
        display: block;
        text-align:center;
    }
    tbody td:before {
        content: attr(data-th);
        display: block;
        text-align:center;
    }
    .img-tabla {
        display:block;
        margin-left: auto;
        margin-right: auto;
        width: 50%;
    }
}
.form2 {
    margin-bottom: 1rem;
}