.seccion-inferior {
    @include mq2 {
        @include grid3($espaciado)
    }
    h1 {
        font-weight: bold;
    }
    .testimonio {
        margin-bottom: 2rem;
        padding: 6rem;
        background-color: greenyellow;
        border-radius: 1rem;
        .relato {
            text-align: left;
            font-family: $fuente_parrafos;
            font-size: 2rem;
        }
        blockquote {
            padding-left: 3rem;
            position: relative;
            &::before {
                content: '';
                background-image: url(/public/build/img/comilla.svg);
                background-repeat: no-repeat;
                width: 6rem;
                height: 8rem;
                display: block;
                position: absolute;
                left: -2rem;
            }
        }
        .autor {
            font-family: $fuente_principal;
            font-weight: bold;
            text-align: right;
        }
        .coma {
            height: 7rem;
            position: absolute;
            left: -14rem;
            display: block;
        }
    }
}
.grid-blog {
    padding-bottom: 2rem;
    @include mq2 {
        @include grid2($espaciado);
    }
    .img-blog {
    picture {
        margin: 0;
    }
    }
    .texto-blog {
    h1 {
        text-align: justify;
        line-height: normal;
        margin: 0;
        margin-bottom: 2rem;
        padding-top: .5rem;
        font-size: 2rem;
        & span {
            border-bottom: 5px solid green;
        }
    }
    .fecha {
        margin: 0;
        margin-bottom: 1rem;
        font-family: $fuente_parrafos;
        & span {
            color: orange;
            font-family: $fuente_parrafos;
        }
    }
    .consejos {
        font-family: $fuente_parrafos;
        text-align: justify;
        margin: 0;
    }
    }
}

