.footer {
    margin-top: auto;
    background-color: $negro;
    padding: 2rem;
    .navegacion-enlaces {
        display: flex;
        flex-direction: column;
        text-align: center;
        justify-content: center;
        @include mq4 {
            flex-direction: row;
        }
    }
    .bot {
        font-size: 1.8rem;
        font-family: $fuente_secundaria;
        color: $blanco;
        margin-right: 1rem;
        &:last-of-type {
            margin-right: 0;
        }
    }
    .bot:hover {
        cursor: pointer;
        color: $amarilo;
    }
    .copy-text {
        font-family: $fuente_principal;
        font-size: 2rem;
        font-weight: 400;
        color: $blanco;
        margin: 0;
    }
    .tit-redes {
        color: $blanco;
        font-family: $fuente_principal;
        font-size: 2rem;
        text-align: center;
    }
    .iconos {
        text-align: center;
    }
}