.no-webp .seccion2 {
    background-image: url('/build/img/encuentra.jpg');
}
.webp .seccion2 {
    background-image: url('/build/img/encuentra.webp');
}
.seccion2 {
    background-position: center;
    text-align: center;
    height: 45rem;
    @include mq2 {
        height: 25rem;
    }
    h1 {
        padding-top: 3.5rem;
        color: $blanco;
        font-weight: bold;
        margin-bottom: 1rem;
        line-height: normal;
    }
    p {
        font-family: $fuente_parrafos;
        color: $blanco;
        margin-top: 0;
        text-align: center;
        margin-bottom: 2.5rem;
    }
    .bot4 {
        background-color: orange;
        padding: 1rem;
        color: $blanco;
        margin-bottom: 0;
        &:hover {
            cursor: pointer;
            background-color: orangered;
        }
    }
}